@media (max-width: 1000px) {
	.ba-heared__history p {
		font-size: 14px;
		font-weight: 400;
		line-height: 25px;
	}

	.ba-heared__history button {
		margin-top: 10px;
	}

	.ba-main__whyUs {
		padding-top: 110px;
	}
}

@media (max-width: 768px) {
	.ba-header__text1 {
		margin-top: 80px;
	}

	.ba-heared__history p {
		padding-right: 0px;
	}
	.ba-heared__history button {
		padding-right: 0px;
	}

	.ba-heared__history {
		padding-top: 50px;
	}

	.ba-header__bear {
		padding-top: 50px;
	}

	.bear {
		display: none;
	}

	.ba-header {
		height: 750px;
	}
	
	
}


@media (max-width: 1200px) {
	.bottom-menu li {
		margin-right: 15px;
	}
}

@media (max-width: 992px) {
	.menu__category li {
		display: inline;
	}

	.ba-category img {
		margin: 10px 0;
	}

	.img-left {
	margin-left: 0px;
	}

	.img_down {
	padding-top: 0px;
	}

	.bottom-menu, .ba-footer__text {
		text-align: center;
	}

	.logo:first-child {
		margin-left: 0px;
	}

	.ba-footer__social {
		margin: 0 auto;
	}
}

@media (max-width: 768px) {
	.ba-form__right {
		margin-top: 0px;
	}

	.img-center{
		text-align: center;
	}
}

@media (max-width: 513px) {
	.ba-header__text2 {
		font-size: 38px;
	}

	.bottom-menu li {
    margin-right: 5px;
	}

	.ba-heared__history h1 {
		margin-top: 0px;
	}

	.ba-heared__history button {
		margin-top: -5px;
	}
}